import React from 'react';
import styled from 'styled-components';
import './css/Navigation.css';
import image from "../images/ImageAboutFirst.jpg";
import {Link} from 'react-router-dom';

    const LeftColumn = styled.div`
        img{
            width: 480px;
            height: 680px;
            border-radius: 5px;
        }
        @media screen and (max-width: 790px){
            img{
                width: 300px;
                height: 350px;
                margin-top: 50px;
            }
                display: flex;
                justify-content: center;
                align-items: center;
        }
    `;
    const RightColumn = styled.div`
        .Right-column{
            max-width: 800px;
            font-size: 18px;
            margin: 25px;
        }
        h2{
            font-size: 34px;
            font-family: 'Lobster', cursive;
            font-weight: 500;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        @media screen and (max-width: 790px){
            .Right-column{
                font-size: 16px;
                margin: 20px
            }
        }
        @media screen and (max-width: 450px){
            h2{
                font-size: 32px;
            }

        }
    `;
    const SectionAbout = styled.div`
    display: flex;
    padding: 5rem 10rem;
    margin-top: 50px;

    @media screen and (max-width: 790px){
        display: flex;
        flex-direction: column-reverse;
        }
    @media screen and (max-width: 450px){
        padding: 1.5rem ;
    }
    `
    const AboutButton = styled(Link)`
    color: #EDFFEC;
    padding: 10px;
    background-color: #5A5766;
    border-radius: 6px;
    position: relative;
    left: 378px;
    a{
        color: #EDFFEC;
    }
    a:hover{
        color: #EDFFEC;
        text-decoration: none;
    }
    @media screen and (max-width: 790px){
        left: 180px;
        }
    @media screen and (max-width: 450px){
        left: 135px;
    }
    `;
const About = () => {

    return ( 
        <SectionAbout id="about">
            <LeftColumn>
                <img src={image} alt="pg"/>
            </LeftColumn>
            <RightColumn>
                <h2>About us</h2>
                <p className="Right-column">
                    We are dynamic growing company with a lot of experiences 
                    in our field. Our team is  built from profesional 
                    developers with a lot of experiences in developing a profesional 
                    web applications and creating fully responsive websites.
                    Programming isn't just making products for someone to get money,
                    programming is the creation the way to give you help hand, 
                    to help your business, give him chance to develop yourself...
                </p>
                <p className="Right-column">
                    All our developer are highly skilled engineers with a experiences
                    from huge companies and also with a lot of experiences in freelancing world.
                    We Are Ready to put all our power to your ideas to make them alive.
                     With our help you can get mine more then more from your ideas because 
                    will help you as it's our own project...
                </p>
                <div className="Last-about-us">
                    <p className="Right-column">
                        I believe we have approached you and we will become a  good partners. 
                    </p>
                    <AboutButton><a href="#contacts">Contact us</a></AboutButton>
                </div>
            </RightColumn>
        </SectionAbout>
     );
}
 
export default About;