import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';

const AllProjects = styled.div`
display: flex;
justify-content: center;
align-items: center;

@media screen and (max-width: 790px){
    justify-content: center;
    align-items: center;
    flex-direction: column-reverse;
}
`;
const SingleProject = styled(Link)`
    background: rgb(34,193,195);
    background: linear-gradient(0deg, rgba(34,193,195,1) 0%, rgba(253,187,45,1) 100%);
    margin: 5px;
    width: 550px;
    height: 200px;
    border-radius: 5px;
    text-decoration: none;
@media screen and (max-width: 790px){
    max-width: 450px;
}
@media screen and (max-width: 450px){
    max-width: 300px;
}
`;
const ProjectHeader = styled(Link)`
    display: flex;
    justify-content: center;
    align-items: center;
    vertical-align: middle;
    margin-top: 70px;
    text-decoration: none;
    color: #EDFFEC;

    &:hover{
        text-decoration: none;
        color: #EDFFEC;
    }
    
`;

const WholeProjectSection = styled.div`
    padding: 5rem 10rem;
    margin-top: 50px;
    @media screen and (max-width: 450px){
        padding: 1.5rem;
    }
`;

const AllProjectsHeader = styled.div`
        font-size: 34px;
        font-family: 'Lobster', cursive;
        font-weight: 500;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 50px;
    
    @media screen and (max-width: 450px){
        font-size: 32px;
    }
    
`;

const Projects = ({projects}) => {
    return ( 
        <WholeProjectSection id="projects">
            <AllProjectsHeader>
                Our Projects
            </AllProjectsHeader>
            <AllProjects>
                {projects.map((project,index)=>{
                    return(
                        <SingleProject to={project.link} key={index}>
                            {
                                <ProjectHeader to={project.link}>
                                    <h1>{project.title}</h1>
                                </ProjectHeader>
                            }
                        </SingleProject>
                    )
                })}
            </AllProjects>
        </WholeProjectSection>
        
     );
}
 
export default Projects;