import React from 'react';
import styled from 'styled-components';
import HeaderPartP from './Particles';
 
const Header = () => {
const Section = styled.div``;
const TextPart = styled.div`
    position: absolute;
    top: 285px;
    left: 50px;
    color: #EDFFEC;
    font-family: 'Lobster', cursive;

    h1{
        font-size: 62px;
    }
    p{
        font-size: 18px;
        box-shadow: 1px 1px 1px 3px;
        margin: 2px 2px 2px 2px;
    }

    @media screen and (max-width: 790px){
        top: 250px;
        left: 25px;

        h1{
            font-size: 38px;
        }
        .header_first{
            font-size: 34px;
            
        }
    }
    @media screen and (max-width: 450px){
        top: 180px;
        left: 5px;

        h1{
            font-size: 18px;
            height: 24px;
        }
        p{
            font-size: 12px;
        }

    }
`;

    return ( 
        <Section>
            <HeaderPartP/>
            <TextPart>
                <div>
                    <h1 className="header_first">Hello, </h1>
                    <h1>We are Star Developement</h1>
                </div>
                
                <p>"Our goal isn’t to build you a website. Our goal is to build your business."</p>
            </TextPart>
        </Section>
     );
}
 
export default Header;