import React from 'react';
import styled from 'styled-components';
import './css/Navigation.css';
import ImageSkills from '../images/Snímka obrazovky (20).png';


const SectionSkills = styled.div`
 margin-top: 50px;
 margin-bottom: 80px;
`;
const HeaderPart = styled.div`
    font-size: 34px;
    font-family: 'Lobster', cursive;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    
`;
const RightPart = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;

    @media screen and (max-width: 790px){
        img{
            max-width: 550px;
            max-height: 500px;
        }
    }
    @media screen and (max-width: 450px){
        img{
            max-width: 340px;
            
        }
    }
`;
const Skills = () => {

    return ( 
        <SectionSkills>
            <HeaderPart>
                <h2>Where we are the best..</h2>
            </HeaderPart>
            <RightPart>
                <img src={ImageSkills} alt=""/>
            </RightPart>
        </SectionSkills>
     );
}

 
export default Skills;
