import React from 'react';
import styled from 'styled-components';
import {AiOutlineInstagram , AiFillFacebook , AiOutlineWhatsApp} from 'react-icons/ai';
import {Link} from 'react-router-dom';


const SectionContacts = styled.div`
    background-color: #5A5766;
    display: flex;
    align-items: center;
    justify-content: center;
`;
const LeftContacts = styled.div`
    li{
        text-decoration: none;
        list-style: none;
        color: white;
    }
    margin: 25px;
`;
const RightContacts = styled.div`
    display: flex;
    flex-direction: column;
    margin-bottom: 0px;
    margin: 25px;
`;
 const IndividualIcons = styled(Link)`
    color: white;
    font-size: 28px;

    &:hover{
        color: #262833;
    }

 `;

const Contacts = () => {
    return ( 
        <SectionContacts id="contacts">
            <LeftContacts>
                <ul>
                    <li><b>Email:</b>  Star@development.com</li>
                    <li><b>Phone:</b>   +421666555</li>
                    <li><b>Adress:</b>   CZ- Prague, Prague 123</li>
                </ul>
            </LeftContacts>
            <RightContacts>
                
                <IndividualIcons>
                    <AiFillFacebook/>
                </IndividualIcons>
                
                <IndividualIcons>
                    <AiOutlineInstagram/>
                </IndividualIcons>

                <IndividualIcons>
                    <AiOutlineWhatsApp/>
                </IndividualIcons>
            
            </RightContacts>
        </SectionContacts>
     );
}
 
export default Contacts;
