import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import {Navbar,  Nav,} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import {FaRegStar} from 'react-icons/fa';
import './css/Navigation.css';



const HeadingTitle = styled(Link)`
    margin-top: 4px;
    margin-left: 10px;
    color: #262833;
    text-decoration: none;

    &:hover{
        color: #262833;
        text-decoration: none;
        style-list: none;
    }
`;
const LogoFull = styled(Link)`
    display: flex;
    flex-direction: row;
    padding: 0.5rem 1rem;
    &:hover{
        text-decoration: none;
    }
`;
const LogoIcon = styled(Link)`
    font-size: 25px;
    color: #262833;
    &:hover{
        color: #262833;
        text-decoration: none;
    }
`;
/*const li = styled(Link)`
    color: #49494a;
    margin: 0.5rem 0.75rem;
    &:hover{
        color: #262833;
        text-decoration: none;
    }
    @media screen and (max-width: 770px){
        margin: 0.1rem;
    }
`;*/

const Navigation = () => {
    return ( 
        <Navbar bg="light" expand="lg">
            <LogoFull to="/" className="logo_full"  href="#home">
                <LogoIcon >
                    <FaRegStar/> 
                </LogoIcon>
                 
                <HeadingTitle className="logo_write">Development</HeadingTitle>
            </LogoFull>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto links-all">
                            <li><a className="links" href="/">Home</a></li>
                            <li><a className="links" href="#about">About</a></li>
                            <li><a className="links" href="#projects">Projects</a></li>
                            <li><a className="links" href="#contacts">Contacts</a> </li>                           
                </Nav>
            </Navbar.Collapse>
        </Navbar>
     );
}
 
export default Navigation;

